// user-menu.component.ts
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-user-menu',
  templateUrl: './user-menu.component.html',
  styleUrls: ['./user-menu.component.scss']
})
export class UserMenuComponent implements OnInit {
  userName: string;
  userRole: number;
  loading: boolean = true;
  maxRetries = 20; // Número máximo de intentos para buscar el usuario en la base de datos
  retryInterval = 2000; // Intervalo entre intentos en milisegundos

  constructor(private router: Router, private authService: AuthService, private userService: UserService, private afAuth: AngularFireAuth) {}

  ngOnInit() {
    this.checkUserInDB();
  }

  checkUserInDB() {
    let attempts = 0;

    const intervalId = setInterval(() => {
      this.afAuth.authState.subscribe(async (user) => {
        if (user) {
          try {
            const dbUser = await this.userService.getUserDB(user.uid);
            if (dbUser) {
              this.userName = dbUser['name'];
              this.userRole = dbUser['role'];
              this.loading = false;
              clearInterval(intervalId);
            }
          } catch (error) {
            console.error('Error fetching user info:', error);
            clearInterval(intervalId);
          }
        }
      });

      attempts++;
      if (attempts >= this.maxRetries) {
        console.error('User not found in DB after maximum retries');
        clearInterval(intervalId);
      }
    }, this.retryInterval);
  }

  navigateTo(route: string) {
    this.router.navigateByUrl(route);
  }

  async logout() {
    await this.authService.logout();
  }
}
