import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/user.service';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.scss'] // Corretto 'styleUrl' in 'styleUrls'
})
export class NavbarComponent implements OnInit {
  userName: string;
  userRole: number;
  currentUser: any;
  loading: boolean = true;
  isAuthenticated: boolean = false;

  constructor(private userService: UserService, private afAuth: AngularFireAuth, private router: Router, public authService: AuthService) {}

  navigateTo(route: string) {
    this.router.navigateByUrl(route);
  }

  ngOnInit() {
    this.authService.user$.subscribe(user => {
      this.isAuthenticated = !!user;
    });
    this.getUserInfo();
  }

  getUserInfo() {
    this.afAuth.authState.subscribe(async (user) => {
      if (user) {
        try {
          this.currentUser = user;
          const dbUser = await this.userService.getUserDB(user.uid);
          if (dbUser) {
            this.userName = dbUser['name'];
            this.userRole = dbUser['role'];
          } else {
            console.error('User not found in DB');
          }
        } catch (error) {
          console.error('Error fetching user info:', error);
        } finally {
          this.loading = false;
        }
      } else {
        this.loading = false;
      }
    }, error => {
      console.error('Error with auth state subscription:', error);
      this.loading = false;
    });
  }
}
