import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { FirebaseConfigurationService } from '../../services/firebase-configuration.service';

@Component({
  selector: 'app-translater',
  templateUrl: './translater.component.html',
  styleUrl: './translater.component.scss'
})
export class TranslaterComponent implements OnInit {
  currentLanguage: string
  languages: { [key: string]: string } = {};
  constructor(private translate: TranslateService, private firebaseConfiguration: FirebaseConfigurationService) {
    this.currentLanguage = this.translate.currentLang;
   }


   ngOnInit() {
    this.firebaseConfiguration.getLanguages().subscribe(languages => {
      this.languages = languages;
    })
  }

  changeLanguage(lang: string) {
    this.currentLanguage = lang.toLowerCase();
    this.translate.use(this.currentLanguage);
    this.translate.setDefaultLang(this.currentLanguage);
  }

  getCurrentLanguageLabel() {
    return this.languages[this.currentLanguage] || 'Loading';
  }

  getLanguageKeys(): string[] {
    return Object.keys(this.languages);
  }

}
