import { Injectable } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/compat/auth';
import { AngularFireDatabase } from '@angular/fire/compat/database';
import { Router } from '@angular/router';
import { Observable, catchError, map, of, switchMap } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  user$: Observable<any | null>;

  constructor(private afAuth: AngularFireAuth, private db: AngularFireDatabase, private router: Router) {
    this.user$ = this.afAuth.authState;
  }

  isLoggedIn(): Observable<boolean> {
    return this.afAuth.authState.pipe(
      map(user => !!user)
    );
  }

  getUserRole(): Observable<number | null> {
    return this.afAuth.authState.pipe(
      switchMap(user => {
        if (user) {
          return this.db.object<{ role: number }>(`/users/${user.uid}`).valueChanges().pipe(
            map(userData => userData ? userData.role : null),
            catchError(error => {
              console.error('Error fetching user role:', error);
              return of(null); // Ritorna null in caso di errore
            })
          );
        } else {
          return of(null); // Ritorna null se l'utente non è autenticato
        }
      }),
      catchError(error => {
        console.error('Error checking auth state:', error);
        return of(null); // Ritorna null in caso di errore nell'autenticazione
      })
    );
  }

  async logout() {
    try {
      await this.afAuth.signOut();
      localStorage.removeItem("userInfo");
      this.router.navigate(['']);
      window.location.reload();
    } catch (error) {
      console.error('Error logging out:', error);
    }
  }
}
