import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { environment } from './environment/environment';


import { AngularFireModule } from '@angular/fire/compat';
import { AngularFireDatabaseModule } from '@angular/fire/compat/database';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { UploadComponent } from './pages/admin/question/upload/upload.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { BreadcrumbComponent } from './components/breadcrumb/breadcrumb.component';
import { QuestionsComponent } from './pages/admin/question/questions/questions.component';
import { NgxPaginationModule } from 'ngx-pagination';
import { QuestionComponent } from './pages/admin/question/question/question.component';
import { TestComponent } from './pages/test/test.component';
import { NgxStripeModule } from 'ngx-stripe';
import { PaymentformComponent } from './components/paymentform/paymentform.component';
import { HttpClient, HttpClientModule } from '@angular/common/http';
import { CertificateComponent } from './components/certificate/certificate.component';
import { SuccessComponent } from './pages/success/success.component';
import { CertificatesComponent } from './pages/user/certificates/certificates.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { UnsubscribeComponent } from './components/unsubscribe/unsubscribe.component';
import { FooterComponent } from './components/footer/footer.component';
import { TermsComponent } from './pages/terms/terms.component';
import { PolicyComponent } from './pages/policy/policy.component';
import { UserMenuComponent } from './components/user-menu/user-menu.component';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslaterComponent } from './components/translater/translater.component';
import { HomeComponent } from './pages/home/home.component';
import { HomePopupComponent } from './components/home-popup/home-popup.component';
import { UsersComponent } from './pages/admin/users/users.component';
import { LoginComponent } from './pages/home/login/login.component';
import { PriceComponent } from './pages/price/price.component';
import { WhatIsIqComponent } from './pages/what-is-iq/what-is-iq.component';
import { NgChartsModule } from 'ng2-charts';
import { ConfigurationComponent } from './pages/admin/configuration/configuration.component';
import { FirebaseConfigurationService } from './services/firebase-configuration.service';


// Función de carga del archivo de traducción
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}
@NgModule({
  declarations: [
    AppComponent,
    UploadComponent,
    BreadcrumbComponent,
    QuestionsComponent,
    QuestionComponent,
    TestComponent,
    PaymentformComponent,
    CertificateComponent,
    SuccessComponent,
    CertificatesComponent,
    NavbarComponent,
    UnsubscribeComponent,
    FooterComponent,
    TermsComponent,
    PolicyComponent,
    UserMenuComponent,
    TranslaterComponent,
    UsersComponent,
    LoginComponent,
    PriceComponent,
    WhatIsIqComponent,
    ConfigurationComponent,
  ],
  imports: [
    BrowserModule,
    AppRoutingModule,
    AngularFireModule.initializeApp(environment.firebase),
    AngularFireDatabaseModule,
    NgbModule,
    ReactiveFormsModule,
    CommonModule,
    NgxPaginationModule,
    FormsModule,
    HttpClientModule,
    TranslateModule.forRoot({
      defaultLanguage: 'en',
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    NgxStripeModule,
    NgChartsModule
  ],
  providers: [],
  bootstrap: [AppComponent],
  exports: [TranslateModule]
})
export class AppModule {
  constructor(private firebaseConfigService: FirebaseConfigurationService, private ngxStripeModule: NgxStripeModule) {
    this.setupStripe();
  }

  private setupStripe() {
    this.firebaseConfigService.getPublicKey().subscribe(publicKey => {
      NgxStripeModule.forRoot(publicKey);
    });
  }
}
