import { CanActivateFn, Router } from '@angular/router';
import { AuthService } from '../services/auth.service';
import { map, take } from 'rxjs';
import { inject } from '@angular/core';

export const roleAuthGuard: CanActivateFn = (route, state) => {
  const authService = inject(AuthService);
  const router = inject(Router);

  return authService.getUserRole().pipe(
    take(1),  // Assicura che il guard verifichi il ruolo solo una volta
    map(role => {
      if (role === 1) {
        return true;
      } else {
        // Redirect to an unauthorized page or login page if the user does not have the correct role
        router.navigate(['/unauthorized']);
        return false;
      }
    })
  );
};
