import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { UploadComponent } from './pages/admin/question/upload/upload.component';
import { QuestionsComponent } from './pages/admin/question/questions/questions.component';
import { QuestionComponent } from './pages/admin/question/question/question.component';
import { TestComponent } from './pages/test/test.component';
import { CertificateComponent } from './components/certificate/certificate.component';
import { SuccessComponent } from './pages/success/success.component';
import { CertificatesComponent } from './pages/user/certificates/certificates.component';
import { userAuthGuard } from './guard/user-auth.guard';
import { roleAuthGuard } from './guard/role-auth.guard';
import { UnsubscribeComponent } from './components/unsubscribe/unsubscribe.component';
import { TermsComponent } from './pages/terms/terms.component';
import { PolicyComponent } from './pages/policy/policy.component';
import { HomeComponent } from './pages/home/home.component';
import { UsersComponent } from './pages/admin/users/users.component';
import { LoginComponent } from './pages/home/login/login.component';
import { loginGuard } from './guard/login.guard';
import { PriceComponent } from './pages/price/price.component';
import { WhatIsIqComponent } from './pages/what-is-iq/what-is-iq.component';
import { ConfigurationComponent } from './pages/admin/configuration/configuration.component';

const routes: Routes = [
  { path: '', loadChildren: () => import('./pages/home/home.module').then(m => m.HomeModule) },
  {
    path: 'question',
    data: { breadcrumb: 'Question' },
    canActivate: [roleAuthGuard],
    loadChildren: () => import('./pages/admin/question/question.module').then(m => m.QuestionModule)
  },
  {
    path: 'users',
    data: { breadcrumb: 'Admin Users' },
    canActivate: [roleAuthGuard],
    loadChildren: () => import('./pages/admin/users/users.module').then(m => m.UsersModule)
  },
  {
    path: 'configuration',
    data: { breadcrumb: 'App configuration' },
    canActivate: [roleAuthGuard],
    loadChildren: () => import('./pages/admin/configuration/configuration.module').then(m => m.ConfigurationModule)
  },
  {
    path: 'test',
    loadChildren: () => import('./pages/test/test.module').then(m => m.TestModule)
  },
  {
    path: 'success',
    loadChildren: () => import('./pages/success/success.module').then(m => m.SuccessModule)
  },
  {
    path: 'user',
    data: { breadcrumb: 'User' },
    canActivate: [userAuthGuard],
    loadChildren: () => import('./pages/user/user.module').then(m => m.UserModule)
  },
  {
    path: 'unsubscribe',
    loadChildren: () => import('./components/unsubscribe/unsubscribe.module').then(m => m.UnsubscribeModule)
  },
  {
    path: 'terms',
    loadChildren: () => import('./pages/terms/terms.module').then(m => m.TermsModule)
  },
  {
    path: 'privacy',
    loadChildren: () => import('./pages/policy/policy.module').then(m => m.PolicyModule)
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/home/login/login.module').then(m => m.LoginModule),
    canActivate: [loginGuard]
  },
  {
    path: 'price',
    loadChildren: () => import('./pages/price/price.module').then(m => m.PriceModule)
  },
  {
    path: 'what-is-iq',
    loadChildren: () => import('./pages/what-is-iq/what-is-iq.module').then(m => m.WhatIsIqModule)
  },
  {
    path: '**', redirectTo: '', pathMatch: "full"
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true, scrollPositionRestoration: 'top', anchorScrolling: 'enabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
